.container-form{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.card-style{
    background-image: url(../images/background.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 400px;
    height: 500px;
    border-radius: 10px;
}
.btn-container{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.first-text{
    font-size: 20px;
    font-weight: bold;
    
}
.second-text{
    font-size: 16px;
    font-weight: bold;
    color: green;
    
}
.first-text span{
    color: red;
}
.text-color{
    color: lightblue;
}
.result{
    background-color: aliceblue;
}
